import { StatusBar } from 'expo-status-bar';
import { useState } from 'react';
import { StyleSheet, Text, View, Button, Alert, Linking} from 'react-native';
import DialogInput from 'react-native-dialog-input'; 


function Resource(props) {
    const [value, setValue] = useState(props.maxValue);
    return (
        <View>
            <Text> </Text>
            <Text>{props.name} {value}/{props.maxValue}<Button title="-" onPress={() => setValue(Math.max(value - 1, 0))} />
                <Button title="+" onPress={() => setValue(Math.min(value + 1, props.maxValue))} /></Text>
        </View>
    );
}


export default function App() {
    const [chopResult, setChop] = useState('N/A');//need a better name for this variable then value
    const [blood, setBlood] = useState(12);
    const [willpower, setWillpower] = useState(6);
    const [health, setHealth] = useState(9);
    const chop_chart = { 1: "Rock", 2: "Paper", 3: "Scissors" };
    const [character, updateCharacter] = useState({ name: "Daria Gray", maxBlood: 12, maxHealth: 9, maxWillpower: 6 });
    let [nameDialogVisible, showDialog] = useState(false);

    function random_chop() {
        let result = Math.ceil(Math.random() * 3);
        setChop(chop_chart[result]);
        Alert.alert(chop_chart[result]);
    }

    function sendInput(text) {
        Alert.alert(text);
        showDialog(false);
    }
  return (
      <View style={styles.container}>
          <Text>{character.name}</Text>
          <Text>{chopResult}</Text>
          <Button title="chop!" onPress={random_chop} />
          <DialogInput isDialogVisible={nameDialogVisible}
              title={"DialogInput 1"}
              message={"Message for DialogInput #1"}
              hintInput={"HINT INPUT"}
              submitInput={(inputText) => { sendInput(inputText) }}
              closeDialog={() => { showDialog(false) }}>
          </DialogInput>

          <Resource name="Blood" maxValue={character.maxBlood} />
          <Resource name="Willpower" maxValue={character.maxWillpower} /> 
          <Resource name="Health" maxValue={character.maxHealth} />

          <Text>Mental Defense = 10 + willpower</Text> 
          <Text>Social Defense = 10 + willpower</Text> 
          <Text>Dodge = 21</Text>
          <Text>Initiative = 20</Text>
          <Text>Melee 19 (accurate sword)</Text>
          <Text> Convert 2 agg, soak 1 normal</Text> 
          <Text> 3 actions/turn</Text> 
          <Text>2 blood/turn</Text>

          <Text> </Text>
          <Button title="More info" onPress={() => Linking.openURL("https://vamp.bynightstudios.com/")}/>
      <StatusBar style="auto" />
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
    alignItems: 'center',
    justifyContent: 'center',
  },
});
